import React from 'react'
import Image from 'next/image';

const companyInfraStructure = [
    { id: '1', image: '/yellow.png', label: 'Network Optimization', discp: 'Streamlining network performance for reliable connectivity.' },
    { id: '2', image: '/gray.png', label: 'Network Optimization', discp: 'Streamlining network performance for reliable connectivity.' },
    { id: '3', image: '/yellow.png', label: 'Network Optimization', discp: 'Streamlining network performance for reliable connectivity.' },
    { id: '4', image: '/gray.png', label: 'Network Optimization', discp: 'Streamlining network performance for reliable connectivity.' },
  ];
  
export default function CompanyStructure() {
    return (
        <>
            <div className='grid  px-4 md:px-12 lg:px-24 md:py-4 content-center '>
                <div className='grid grid-cols-1 lg:grid-cols-2 justify-between lg:gap-28'>

                    <div className='flex flex-col py-12 lg:py-0'>
                        <h1 className='text-3xl md:text-4xl lg:text-4xl font-medium  '>IT Infrastructure <br />  Management</h1>
                        <h1 className='text-sm md:text-md lg:text-lg  font-medium text-[#00000099] pt-4 pb-16 '>End-to-end solutions for network management, cloud integration, and infrastructure optimization.</h1>
                        <Image
                            src="/ITeqpt.jpeg"
                            width={487}
                            height={311}
                            className="lg:w-96 lg:h-56  md:w-96 md:h-56 rounded-[40px] object-cover"
                            alt="Hero section image"
                        />
                    </div>
                    <div className='grid grid-cols-1 items-center justify-center gap-8 md:gap-x-10 md:gap-y-7 lg:gap-x-20 lg:gap-y-5 md:grid-cols-1'>
                        {companyInfraStructure.map((structure) => {
                            return (
                                <div
                                    key={structure.id}
                                    className=" flex flex-row    "
                                >
                                    <Image width={106} height={106} alt='IT Imge' src={structure.image} className='w-20 h-20 rounded-3xl' />
                                    <div className='flex flex-col ml-4 lg:ml-8 justify-center '>
                                        <p className="text-xs md:text-xl lg:text-lg font-semibold ">{structure.label}</p>
                                        <p className="text-xs md:text-sm lg:text-xs font-medium text-[#00000099] py-3">{structure.discp}</p>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}
